import React from 'react';
import { useQuery } from '@tanstack/react-query';
import { useFormik } from 'formik';

import Box from '@mui/material/Box';
import Divider from '@mui/material/Divider';
import Button from '@mui/material/Button';

import { Loader, TextField, SelectField } from '../components';

import { CustomerQuery } from '../api';
import { useSnackbar } from '../hooks';
import { CreateCustomer, CustomerTypes, FormOnClose } from '../utils/dataTypes';

const initialValues = {
  id: '',
  name: undefined,
  type: undefined,
  address: undefined,
  address2: undefined,
  city: undefined,
  state: undefined,
  zipCode: undefined,
  phone: undefined,
  fax: undefined,
  email: undefined,
  website: undefined,
};

export default function ViewCustomerForm({
  id,
  onClose,
}: {
  id: string;
  onClose: FormOnClose;
}) {
  const setSnackbarMessage = useSnackbar();

  const {
    isLoading: loading,
    isFetching: fetching,
    error,
    data,
  } = useQuery({
    queryKey: ['customer', id],
    queryFn: CustomerQuery,
    retry: false,
  });
  React.useEffect(() => {
    if (!error) {
      return;
    }
    setSnackbarMessage({
      severity: 'error',
      message: error instanceof Error ? error.message : JSON.stringify(error),
    });
  }, [error]);

  const formik = useFormik<CreateCustomer>({
    initialValues: {
      ...initialValues,
      ...data,
    },
    enableReinitialize: true,
    onSubmit: () => {
      return;
    },
  });

  const handleClose = () => {
    onClose('Close');
  };

  const handleEdit = () => {
    onClose('Edit', data);
  };

  return (
    <Box sx={{ height: '100%' }}>
      <form
        onSubmit={formik.handleSubmit}
        onReset={formik.handleReset}
        style={{ height: '100%' }}
      >
        <Box sx={{ p: 3, height: '100%' }}>
          <Divider />
          <Box
            key="controls"
            sx={{
              maxHeight: 'calc(100% - 70px)',
              overflowX: 'hidden',
              overflowY: 'scroll',
            }}
          >
            <Box
              key="centerControls"
              sx={{ display: 'inline-flex', width: '100%' }}
            >
              <Box key="left" sx={{ p: 1, width: '50%' }}>
                <TextField
                  fieldName="name"
                  label="Company Name"
                  disabled
                  formik={formik}
                />
                <TextField
                  fieldName="address"
                  label="Billing Address Line 1"
                  placeholder="Building number and street name"
                  disabled
                  formik={formik}
                />
                <TextField
                  fieldName="phone"
                  label="Phone"
                  disabled
                  formik={formik}
                />
                <TextField
                  fieldName="fax"
                  label="Fax"
                  disabled
                  formik={formik}
                />
              </Box>
              <Box key="right" sx={{ p: 1, width: '50%' }}>
                <SelectField
                  fieldName="type"
                  label="Customer Type"
                  items={CustomerTypes}
                  disabled
                  formik={formik}
                />
                <TextField
                  fieldName="address2"
                  label="Billing Address Line 2"
                  placeholder="Apartment#, suite#, box#, etc"
                  disabled
                  formik={formik}
                />
                <TextField
                  fieldName="email"
                  label="Email"
                  placeholder="Enter Email address"
                  disabled
                  formik={formik}
                />
                <TextField
                  fieldName="website"
                  label="Website"
                  placeholder="Enter company`s URL"
                  disabled
                  formik={formik}
                />
              </Box>
            </Box>
            <Box
              key="bottomControls"
              sx={{
                marginTop: -2,
                marginBottom: 0,
                p: 1,
                paddingBottom: 0,
              }}
            >
              <TextField
                fieldName="city"
                label="City"
                placeholder="Enter city"
                disabled
                formik={formik}
              />
              <TextField
                fieldName="state"
                label="State"
                placeholder="Enter state"
                disabled
                formik={formik}
              />
              <TextField
                fieldName="zipCode"
                label="Zip"
                placeholder="Enter ZIP"
                disabled
                formik={formik}
              />
            </Box>
          </Box>
          <Divider />
          <Box key="buttons" sx={{ paddingTop: 3, display: 'flex' }}>
            <Box key="left" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleEdit}
              >
                Edit
              </Button>
            </Box>
            <Box key="right" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleClose}
              >
                Cancel
              </Button>
            </Box>
          </Box>
        </Box>
      </form>
      <Loader visible={loading || fetching} />
    </Box>
  );
}
